import React, {Component} from 'react';
import {connect} from 'react-redux';
//import {Dropdown} from 'react-bootstrap';
import windowSize from 'react-window-size';

import NavSearch from './NavSearch';
import Aux from "../../../../../hoc/_Aux";
import DEMO from "../../../../../store/constant";
import * as actionTypes from "../../../../../store/actions";
import i18next from 'i18next';
import { withTranslation } from 'react-i18next';
import LanguageList from './lang';
class NavLeft extends Component {
    
    render() {
        // const { t, i18n } = this.props;

        // const lang=localStorage.getItem('lang');
        // const selectedLang=lang=="fr"?i18n.t('french-label'):i18n.t('english-label');
        // const changeLanguage = (event) => {
        //     // i18n.changeLanguage(event.target.value);
        //     localStorage.setItem('lang',event.target.name);
      
        //     console.log('language='+event.target.name);
        //     console.log('language stored='+localStorage.getItem('lang'));
        //     console.log("url=>"+window.location.href);
        //     // window.location.reload(false);
        //   }
        let iconFullScreen = ['feather'];
        iconFullScreen = (this.props.isFullScreen) ? [...iconFullScreen, 'icon-minimize'] : [...iconFullScreen, 'icon-maximize'];

        let navItemClass = ['nav-item'];
        if (this.props.windowWidth <= 575) {
            navItemClass = [...navItemClass, 'd-none'];
        }
        // let dropdownRightAlign = false;
        // if (this.props.rtlLayout) {
        //     dropdownRightAlign = true;
      //  }


        return (
            <Aux>
                <ul className="navbar-nav mr-auto">
                    <li><a href={DEMO.BLANK_LINK} className="full-screen" onClick={this.props.onFullScreen}><i className={iconFullScreen.join(' ')} /></a></li>
                    <li className={navItemClass.join(' ')}>
                        <LanguageList/>
                        {/* <Dropdown alignRight={dropdownRightAlign}>
                            <Dropdown.Toggle variant={'link'} id="dropdown-basic">
                             language
                            </Dropdown.Toggle>
                            <ul>
                                <Dropdown.Menu>
                                    <li onClick={this.changeLanguage} name="ar">ar</li>
                                    <li onClick={this.changeLanguage} name="fr">fr</li>
                                    <li onClick={this.changeLanguage} name="en">en</li>
                                </Dropdown.Menu>
                            </ul>
                        </Dropdown> */}
                    </li>
                    <li className="nav-item"><NavSearch/></li>
                </ul>
            </Aux>
        );
    }
}

const mapStateToProps = state => {
    return {
        isFullScreen: state.isFullScreen,
        rtlLayout: state.rtlLayout
    }
};

const mapDispatchToProps = dispatch => {
    return {
        onFullScreen: () => dispatch({type: actionTypes.FULL_SCREEN}),
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(windowSize(withTranslation()(NavLeft)));
