import jwt_decode  from "jwt-decode";
export function  settoken() {
  try {
    if(localStorage.getItem('token')!==null)
    {
      return jwt_decode(localStorage.getItem('token')).roles;
      //return jwt_decode(localStorage.getItem('token')).roles;
    }
    else{ return null ;}
  } catch (error) {
    localStorage.removeItem('token');
    return null ;
  }


  }
export function  logout() {
    //localStorage.removeItem("token")
    localStorage.clear()

 }

 export function verfier(){
  try {
    if(localStorage.getItem('token')!==null && jwt_decode(localStorage.getItem('token')).roles.includes("ROLE_ADMIN"))
    {
      return true;
      //return jwt_decode(localStorage.getItem('token')).roles;
    }
    else{ return null ;}
  } catch (error) {
    localStorage.removeItem('token')
  }

 }